.homepage_container {
  display: flex;
  flex-direction: column;
  justify-content: baseline;
  align-items: center;
  width: 400px;
  margin: 20px 0 50px 0;
}

.homepage_greeting_cointainer {
  display: flex;
  justify-content: center;
}

.homepage_greeting {
  font-family: "Lato", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 24px;
}

.homepage_content {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 18px;
  line-height: 1.5;
  margin: 0;
  padding: 0 1.5rem 0 1.5rem
}

.homepage_container a {
  color: #ff3399;
}

.button_container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 5rem;
  width: 100%;
}

.nav_button {
  background-color: #eee93b;
  border: none;
  border-radius: 10px;
  box-shadow: .2rem .2rem .2rem lightgray;
  font-family: "Oswald", sans-serif;
  font-size: 1.5rem;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
  padding: 10px;
  width: 150px;
}

.nav_button:hover {
  background-color: #ff3399;
  color: #eee93b;
}

.icon_container {
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  box-shadow: 0.3rem -0.3rem 0.3rem lightgray;
  background-color: #eee93b;
  transform: rotate(90deg);
  pointer-events: none;
  margin-top: 50px;
}

.icon_container:hover {
  background-color: #ff3399;
}

.icon_container > svg {
  pointer-events: auto;
}

@media (min-width: 1000px){

  .homepage_container {
    width: 100%;
    margin: 0 0 50px 0;
    justify-content: baseline;
    align-items: center;
  }

  .homepage_greeting_cointainer {
    display: flex;
    justify-content: baseline;
  }
  
  .homepage_greeting {
    font-size: 32px;
    padding: 24px;
  }
  
  .homepage_content {
    font-size: 24px;
  }
  
  .nav_button {
    background-color: #eee93b;
    border: none;
    border-radius: 10px;
    box-shadow: .2rem .2rem .2rem lightgray;
    font-size: 24px;
    padding: 10px;
    width: 200px;
  }
  /* .icon_container {
    transform: rotate(0deg);
  } */

}