.hero_image_container {
  display: flex;
  justify-content: center;
  height: auto;
  width: auto;
  padding: 0;
  margin: 0;
}

img {
  width: 100vw;
}


@media (min-width: 1000px){

  .hero_image_container {
    height: 350px;
    padding: 0;
    margin: 0;
  }

  img {
    width: auto
  }

}